.header{
  padding: 0 1em;
  height: 60px;
  position: fixed;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.header__left, .header__right{
  vertical-align: top;
  display: inline-block;
  height: 100%;
}

.header__right{
  height: 100%;
  float: right;
  margin-right: 0.5em;
}

.header__btn, .header__label{
  height: 100%;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 0.5em;
  position: relative;
}

.header__btn{
  cursor: pointer;
  color: #8499c6;
}

.header__btn:hover{
  color: #cad3e6;
}


